import { CollectionFabrics } from "@/typings/collection";
import { formatImageUrl } from "@/utils/image";
import { getCover } from "@/constants/edm-cover";

const createImage = (data: CollectionFabrics) => {
  const url = encodeURI(data.fabric.image_url);
  return `
  <table
    border="0"
    cellpadding="0"
    cellspacing="0"
    width="100%"
  >
    <tr>
      <td width="180" valign="top">
        <img
          src="${formatImageUrl(url) + "@180-180"}"
          alt="${data.fabric.name}"
          width="180"
          height="180"
          style="display: block;"
        />
      </td>
      <td
        style="font-size: 0; line-height: 0;"
        width="15"
      >
        &nbsp;
      </td>
      <td width="160">
        <h3 style="font-size: 20px; font-weight: 700;">
          ${data.fabric.name}
        </h3>
        <p style="font-size: 18px; font-weight: 600;">
          ${data.fabric.real_composition}
        </p>
      </td>
    </tr>
  </table>
`;
};

const createCover = (
  brandName: string,
  templateId: number,
  message: string
) => {
  return `
  <table
    align="center"
    border="0"
    cellpadding="0"
    cellspacing="0"
    width="100%"
  >
    <tr>
      <td>
        <h1 style="font-size: 20px; font-weight: 800; padding: 0; margin: 0; font-family: Avenir; color: #2c3e50;">
          ${brandName}
        </h1>
      </td>
    </tr>
    <tr>
      <td style="font-size: 0; line-height: 0;" height="15">
        &nbsp;
      </td>
    </tr>
    <tr>
      <td width="740" valign="top">
        <img
          src="${getCover(templateId) + "@740-490"}"
          alt=""
          width="740"
          height="490"
          style="display: block;"
        />
      </td>
    </tr>
    <tr>
      <td style="font-size: 0; line-height: 0;" height="30">
        &nbsp;
      </td>
    </tr>
    <tr>
      <td>
        <p style="font-size: 16px; font-weight: 500; padding: 0; margin: 0; font-family: Avenir; color: #2c3e50;">
          ${message}
        </p>
      </td>
    </tr>
  </table>
  `;
};

const createCollections = (collections: CollectionFabrics[]) => {
  const data = collections.reduce((prev, item, index) => {
    console.log(prev);
    if (!(index % 2)) {
      // @ts-ignore
      prev.push([item]);
    } else {
      // @ts-ignore
      (prev[prev.length - 1] as []).push(item);
    }
    return prev;
  }, []);

  return `
    <table
      border="0"
      cellpadding="0"
      cellspacing="0"
      width="100%"
    >
      ${data.map(
        item => `
          <tr>
            <td width="355" valign="top">
              ${createImage(item[0])}
            </td>
            <td style="font-size: 0; line-height: 0;" height="10">
              &nbsp;
            </td>
            <td width="355" valign="top">
              ${item[1] ? createImage(item[1]) : ""}
            </td>
          </tr>
          <tr>
            <td style="font-size: 0; line-height: 0;" height="30">
              &nbsp;
            </td>
          </tr>
        `
      )}
    </table>
  `;
};

export const createEDMHtml = (
  templateId: number,
  collections: CollectionFabrics[] | undefined,
  text: string,
  brandName: string,
  message: string
) => {
  return `
    <!DOCTYPE html PUBLIC "-//W3C//DTD XHTML 1.0 Transitional//EN" "http://www.w3.org/TR/xhtml1/DTD/xhtml1-transitional.dtd">
      <html xmlns="http://www.w3.org/1999/xhtml">
　      <head>
　　      <meta http-equiv="Content-Type" content="text/html; charset=UTF-8" />
　　        <title>${brandName}</title>
　　      <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
　      </head>
        <body style="margin: 0; padding: 0;">
          <table border="0" cellpadding="0" cellspacing="0" width="100%">
　　        <tr>
              <table
                align="center"
                border="0"
                cellpadding="0"
                cellspacing="0"
                width="800"
              >
                <tr>
                  <td
                    style="padding-top: 30px; padding-right: 30px; padding-bottom: 30px; padding-left: 30px; background-color: #f7f7f7;"
                  >
                    <p style="font-size: 16px; font-weight: 500;  color: #2c3e50;">${text}</p>
                  </td>
                </tr>
                <tr>
                  <td style="font-size: 0; line-height: 0;" height="30">
                    &nbsp;
                  </td>
                </tr>
                <tr>
                  <td
                    style="padding-top: 30px; padding-right: 30px; padding-bottom: 30px; padding-left: 30px; background-color: #f7f7f7;"
                  >
                   ${createCover(brandName, templateId, message)}
                   </td>
                 </tr>
                 <tr>
                   <td style="font-size: 0; line-height: 0;" height="30">
                     &nbsp;
                   </td>
                 </tr>
                ${
                  collections
                    ? `
                      <tr>
                        <td style="padding-top: 30px; padding-right: 30px; padding-left: 30px">
                          ${createCollections(collections)}
                        </td>
                      </tr>
                    `
                    : ""
                }
              </table>
　　        </tr>
          </table>
        </body>
     </html>
  `;
};
