export const COVER_URL = {
  COVER1: "https://weaverbird.bilabasia.com/static/mail/covers/cover_1.png",
  COVER2: "https://weaverbird.bilabasia.com/static/mail/covers/cover_2.png",
  COVER3: "https://weaverbird.bilabasia.com/static/mail/covers/cover_3.png"
};

export const getCover = (templateId: number) => {
  if (templateId === 1) return COVER_URL.COVER1;
  if (templateId === 2) return COVER_URL.COVER2;
  if (templateId === 3) return COVER_URL.COVER3;
  return COVER_URL.COVER1;
};
