<template>
  <div id="edm-dialog-component">
    <el-dialog
      title="Choice Collection"
      v-model="visible"
      width="80%"
      top="45px"
    >
      <el-row :gutter="60">
        <el-col
          :style="{ marginBottom: '60px' }"
          v-for="(item, index) in list"
          :key="index"
          :span="6"
        >
          <CollectionCard
            :data="item"
            @card-click="onCardClick"
          ></CollectionCard>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, onMounted } from "vue";
import { useStore } from "vuex";

import CollectionCard from "@/components/CollectionCard.vue";
import { GetCollections } from "@/typings/store";

const Component = defineComponent({
  name: "EDMDialog",
  components: {
    CollectionCard
  },
  setup(_, { emit }) {
    const store = useStore();

    const visible = ref(false);

    const list = computed(() => store.state.collection.list);

    onMounted(async () => {
      await store.dispatch(GetCollections, {});
    });

    const onCardClick = (id: number) => {
      emit("select", id);
      visible.value = false;
    };

    const open = () => {
      visible.value = true;
    };

    return {
      visible,
      list,

      onCardClick,
      open
    };
  }
});

export default Component;
</script>
